*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  overflow-wrap: break-word;
  user-select: text;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #20232a;
  font-family: TTNorms, sans-serif;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  margin: 0;
  border: none;
  padding: 0;
  outline: none;
}

button {
  border: none;
  padding: 0;
  cursor: pointer;
  outline: none;
}

ol,
ul {
  padding-left: 0;
}

dl,
ol,
ul {
  margin-top: 0;
  margin-bottom: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  margin-bottom: 0;
}

.containers {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

.display-n {
  display: none !important;
}

opacity-h {
  opacity: 0 !important;
}

.welcome {
  width: 100%;
}

.welcome .containers {
  position: relative;
}

.welcome__logo {
  position: absolute;
  top: -55%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -65%);
}

.welcome__content {
  position: relative;
  z-index: 1;
  width: 60%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  grid-template-rows: auto;
  gap: 30px;
}

.welcome__item {
  width: 100%;
}

.welcomeCard {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 12px;
  width: 100%;
  min-height: 120px;
  border: 2px solid #61dafb;
  padding: 18px;
  border-radius: 20px;
  background-color: #20232a;
}

.welcomeCard:hover img {
  transform: translateX(10px);
}

.welcomeCard:hover p {
  color: #fff;
}

.welcomeCard__title {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 0;
}

.welcomeCard__title h5 {
  font-weight: 600;
  font-size: 16px;
  line-height: 23px;
  color: #61dafb;
  font-style: normal;
}

.welcomeCard__title img {
  transition: all 0.5s ease;
  width: 35px;
  height: 25px;
}

.welcomeCard__text {
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #61dafb;
  font-style: normal;
  transition: all 0.5s ease;
}

@media screen and (max-width: 1050px) {
  .welcome__logo {
    top: -25%;
  }
}

@media screen and (max-width: 550px) {
  .welcome__content {
    width: 100%;
  }
}

@media screen and (max-width: 450px) {
  .welcome__content {
    grid-template-columns: 1fr;
  }
}